export const getCurrentTokenBalance = (publickey) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!erc20_contract) {
        erc20_contract = await getERC20Contract()
      }

      let balance = await erc20_contract.methods.balanceOf(publickey).call()
      balance = web3.utils.fromWei(balance, 'ether')
      resolve(balance)

    } catch (err) {
      reject(err)
    }
  })
}

const getMarketContract = () => {
  return new Promise(async (resolve, reject) => {
    if (!window.ethereum) {
      reject('Connect Metamask')
    }
    if (!web3) {
      web3 = new Web3(window.ethereum)
    }
    let contract = await new web3.eth.Contract(
      marketJson.abi,
      MARKET_CONTRACT_ADDRESSS,
    )

    resolve(contract)
  })
}
const getERC721Contract = () => {
  return new Promise(async (resolve, reject) => {
    if (!window.ethereum) {
      reject('Connect Metamask')
    }
    if (!web3) {
      web3 = new Web3(window.ethereum)
    }
    let contract = await new web3.eth.Contract(
      erc721Json.abi,
      CHILLISWAP_CONTRACT_ADDRESSS,
    )

    resolve(contract)
  })
}
const getERC20Contract = () => {
  return new Promise(async (resolve, reject) => {
    if (!window.ethereum) {
      reject('Connect Metamask')
    }
    if (!web3) {
      web3 = new Web3(window.ethereum)
    }
    let contract = await new web3.eth.Contract(
      erc20Json.abi,
      TOKEN_CONTRACT_ADDRESSS,
    )

    resolve(contract)
  })
}

export const verifyAuction = (artwork) => {
  return new Promise(async(resolve, reject)=>{
    //{ active, seller, value, endTime, owner, currency } 
  try{
    let auction = await getAuction(artwork.tokenId)
    if(!artwork.title || !artwork.description || !artwork.image){
      console.log("bs not sycned")
      resolve({isSync: false})
      return
    }


    if (auction.active) {
      if (auction.value !== artwork.price || auction.endTime !== artwork.endTime || auction.currency  !== artwork.currency ) {
        console.log("abs not sycned")
        resolve({isSync: false})
        return
      }else if((artwork.bidders).length > 0 && auction.owner !== artwork.owner){
        console.log("ow not sycned")
          resolve({isSync: false})
          return
        }
    } else {
      if (auction.active !== artwork.isAuction) {
        console.log("au not sycned")
        resolve({isSync: false})
        return
      }
    }

    resolve({isSync: true})
    return
  }catch(err){
    reject(err)
    return
  }


  })
}


export const getAuction = (tokenId) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!market_contract) {
        market_contract = await getMarketContract()
      }
      let {active, seller, value, endTime, owner, currency} = await market_contract.methods.getAuction(tokenId).call()
      const data = {
        active,
        seller: seller.toLowerCase(),
        value: parseInt(value),
        endTime,
        owner: owner.toLowerCase(),
        currency: currency == 0? "ETH": "KOKO"
      }
      resolve(data)
    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}



export const mintToken = (metadataHash, artwork, royalty) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!erc721_contract) {
        erc721_contract = await getERC721Contract()
      }
      let publicAddress = await getCurrentAccount()

      let mintedNFT = await erc721_contract.methods
      .mintAndApproveNFT(publicAddress,  metadataHash, artwork, royalty) // .mintNFT(
      .send({ from: publicAddress })
      .on('transactionHash', function (hash) {})
      .on('confirmation', function (confirmationNumber, receipt) {})
      .on('receipt', function (receipt) {})

      resolve(mintedNFT)
      
    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}
