export const placeBet = (tokenId, price, currency) => {
  return new Promise(async (resolve, reject) => {
    try {
      if (!market_contract) {
        market_contract = await getMarketContract()
      }
      if (!erc20_contract) {
        erc20_contract = await getERC20Contract()
      }
      let publicAddress = await getCurrentAccount()
      let value = 0
      let tokenValue = 0
      if(currency == "ETH"){
        value = web3.utils.toWei(price.toString(), 'ether')
      }else{
        tokenValue = web3.utils.toWei(price.toString(), 'ether')

        let allowedValue = await erc20_contract.methods.allowance( publicAddress,MARKET_CONTRACT_ADDRESSS).call()
        if(allowedValue < tokenValue){
          let approvedTX = await erc20_contract.methods
          .approve(MARKET_CONTRACT_ADDRESSS, tokenValue)
          .send({ from: publicAddress})
          .on('transactionHash', function (hash) {})
          .on('confirmation', function (confirmationNumber, receipt) {})
          .on('receipt', function (receipt) {
            console.log(receipt)
          })
        }

      }

      let bidedTx = await market_contract.methods
      .bid(tokenId, tokenValue)
      .send({ from: publicAddress, value: value })
      .on('transactionHash', function (hash) {})
      .on('confirmation', function (confirmationNumber, receipt) {})
      .on('receipt', function (receipt) {})
      
      resolve(bidedTx)

    } catch (err) {
      console.log(err)
      reject(err)
    }
  })
}
