const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
}

//temp added for auction
const renderTimmer = (isAuction, endTime) => {
  if (isAuction) {
    let dif = parseInt(endTime) - parseInt(Math.floor(Date.now() / 1000));
    return (
      <div className="col-xl-8 col-8">
        <h5>Auction Ending In</h5>
        {dif > 0 ? (
          <Countdown type="mini-countdown" endTime={endTime} />
        ) : (
          <h6>Auction has Ended</h6>
        )}
      </div>
    );
  }
};

const RenderCreator1 = (creatorId) => {
  const creator = useSelector((state) => selectCreatorById(state, creatorId))

  const avatar = creator && creator.avatar ? IMG_BASE+ creator.avatar : AvatarImg
  const username = creator && creator.username ? creator.username : 'unknown'
  return (
    <>
  
      <div className="col-xl-3 mt-5 col-12 d-flex">
        <img src={ avatar + "?w=50&h=50"} className="img-fluid  creator-image " style={{marginLeft:'-30px'}} alt="creator"/>
        <p className="item-content-p" style={{marginLeft:'-50px', marginTop:'10px'}} >@{username}</p>
      </div>
      
      
    </>
  )
}

