import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  selectLiveArtworks,
  selectSoldArtworks,
  selectReservedArtworks
} from '../../store/artwork/artworkSlice'

import ArtworkCard from '../../components/Card/ArtworkCard'
import ArtworkSkeleton from './Skeleton/ArtworkSkeleton'
import cubes from '../../images/figures.png'
// import { artworkAPI } from '../../api/artwork'

const tabsList = ['Live', 'Sold', 'Reserved']

const Artworks = (props) => {
  const liveArtworks = useSelector((state) => selectLiveArtworks(state))
  const soldArtworks = useSelector((state) => selectSoldArtworks(state))
  const reservedArtworks = useSelector((state) => selectReservedArtworks(state))
  const artworkStatus = useSelector((state) => state.artwork.status)
  const [activeTab, setActiveTab] = useState('Live')
  const [isOpened, setIsOpened] = useState(false);

  function toggle() {
    setIsOpened(wasOpened => !wasOpened);
  }
  let search = window.location.search
  let params = new URLSearchParams(search)
  let selectedCategory = params.get('category')
  let searchedQuery = params.get('search')

  const Tab = ({ tab }) => {
    const handleTab = (e) => {
      e.preventDefault()
      let tab = e.target.dataset.value
      setActiveTab(tab)
    }

    return (
      <div
        className={`nav-item nav-link ${activeTab === tab ? 'active' : ''}`}
        onClick={handleTab}
        data-value={tab}
      >
        {tab}
      </div>
    )
  }

  const RenderArtworks = () => {
    // console.log(liveArtworks)
    // let liveArtworksd = liveArtworks.concat(liveArtworks)
    let artworks = []
    if(activeTab === "Live"){
      artworks = liveArtworks
    }else if(activeTab === "Sold"){
      artworks = soldArtworks
    }else if(activeTab === "Reserved"){
      artworks = reservedArtworks
    }else{
      artworks = liveArtworks.concat(soldArtworks)
    }
  //  let artworks = liveArtworks.concat(soldArtworks)
    if (selectedCategory) {
      artworks = artworks.filter((artwork) => {
        if (selectedCategory === artwork.category) {
          return true
        }
        return false
      })
    }else if(searchedQuery){
        searchedQuery = searchedQuery.toLowerCase()
        artworks = artworks.filter((artwork) => {
        let searchIn = artwork.title? (artwork.title).toLowerCase() : ''
        if(searchIn.search(searchedQuery) == 0){
          return true
        }
        return false
      })
    }

    if(artworkStatus !== "succeeded"){
        return <></>
    }

    if (artworks.length <= 0) {
      return (
        <div
          style={{
            textAlign: 'center',
            display: 'flex',
            flexDirection:'column',
            flexWrap: 'nowrap',
            alignContent: 'center',
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            height: '40vh',
          }}
        >
          <img src={cubes}></img>
          <h2 className="pt-2 pb-5">No Artworks</h2>
        </div>
      )
    } else {
      return (
        <div className="masonry layout hero-mobile">
          {artworks.map((artwork) => {
            return <ArtworkCard key={artwork._id} artwork={artwork} />
          })}
        </div>
      )
    }
  }

  return (
    <section className="artworks">
      <div className="container">
        <div className="row">
          <div className="col-md-12 text-center">
             <nav className="nav-justified col-md-3">
               <div className="nav-select d-flex justify-content-between" onClick={toggle}>
                 <p>Filter</p>
                 <p>&#x25BC;</p>
               </div>
               {isOpened && (
              <div className="nav nav-tabs  " role="tablist">
                {tabsList.map((tab) => {
                  return <Tab key={'navTab_' + tab} tab={tab} />
                })}
              </div>
               )}
              </nav>
               

            <div className="tab-content">
              <div className="">
                <div className="pt-3"></div>

                {RenderArtworks()}
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
export default Artworks
