import {useEffect,useRef } from 'react'

function Clip({ url , className, sound}) {
    const videoRef = useRef();
    const previousUrl = useRef(url);
    if(sound === undefined || sound === null){
      sound = false
    }
    useEffect(() => {
      if (previousUrl.current !== url && videoRef.current) {
        videoRef.current.load();
        previousUrl.current = url;
      }


      return ()=>{
   
      }
    }, [url,sound ]);
  
    return (
      <video className={className} ref={videoRef}  autoPlay={true} loop={true} muted={sound? false: true} playsInline>
        <source src={url} />
      </video>
    );
  }

export default Clip