import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

import Loading from '../../components/Loading'

import { listToken, getAuction,verifyAuction } from '../../services/Web3/token'

import {
  selectArtworkByTokenId,
  fetchAllArtworks,
  fetchArtwork,
  syncAuction
} from '../../store/artwork/artworkSlice'

import Clip from '../../components/Clip'
import { isVideo } from '../../utils'

import { IMG_BASE } from '../../constant/'


const List = (props) => {
  const dispatch = useDispatch()
  let history = useHistory()

  let { tokenId } = props.match.params

  //remove
  tokenId = tokenId.toString()

  const userSlice = useSelector((state) => state.user)
  const artworkSlice = useSelector((state) => state.artwork)

  const artwork = useSelector((state) => selectArtworkByTokenId(state, tokenId))

  const [formData, setFormData] = useState({
    price: 0,
    currency: 'ETH',
    time: 1
  })

  const [buttonState, setButtonState] = useState({
    listing: false,
  })
  const [dailog, setDailog] = useState({
    active: false,
    status: '',
    message: '',
  })

  useEffect(() => {
    let lookingForArtworkInterval = null
    if (!artwork) {
      lookingForArtworkInterval = setInterval(() => {
        console.log('looking for artwork')
        dispatch(fetchArtwork(tokenId))
      }, 5000)
    }
    if (artwork) {
      clearInterval(lookingForArtworkInterval)
      ;(async()=>{
        if(artworkSlice.status !== 'loading'){
           let {isSync} = await verifyAuction(artwork)
           console.log(isSync)
           if(!isSync){
             dispatch(syncAuction({ tokenId: artwork.tokenId }))
           }
         }
       })()
    }

    return () => {
      clearInterval(lookingForArtworkInterval)
    }
  }, [artwork])

  


  





  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
  }

  const handleListNFT = async (e) => {
    e.preventDefault()
    try {
    
      console.log('listing')
      setButtonState({ ...buttonState, listing: true })

      let artworkAuction = await getAuction(tokenId)
      if (artworkAuction.active) {
        dispatch(syncAuction({tokenId: tokenId}))
        throw new Error('Already Listed in Auction' )
      }
     
      console.log(tokenId, formData.price, formData.currency, formData.time)
      await listToken(tokenId, formData.price, formData.currency, formData.time)

      dispatch(fetchAllArtworks())

      setButtonState({ ...buttonState, listing: false })
      history.push(`/${artwork.username}/${artwork.url}`)

      // console.log(savedArtwork)
    } catch (err) {
      setButtonState({ ...buttonState, listing: false })
      let errorMessage = err.message
      if(errorMessage.length > 40){
        errorMessage = "Internal Error"
      }
      setDailog({
        active: true,
        status: 'error',
        message: errorMessage,
      })
      console.log(err)
    }
  }

  if ( !artwork || !userSlice.user || artwork.owner !== userSlice.user.publicAddress) {
    return <Loading />
  }

  return (
    <section className="join-the-community">
      <div className="container pt-5 pb-4">
       {/*} <img
          src="/assets/images/Group-203.svg"
          className="img-fluid community-left-image"
          alt="bg"
  />*/}
        <div className="row">
          <div className="col-xl-9 text-center mx-auto">
            <div className="row">
              <div className="col-lg-5">
                <div className="row">
                  <div className="col-xl-12">
                    <div className="upload-image">
                      {isVideo(artwork.artwork) ? (
                        <Clip url={IMG_BASE + artwork.artwork} className="" />
                      ) : (
                        <img
                          className=""
                          src={IMG_BASE + artwork.artwork}
                          alt="nft"
                        ></img>
                      )}
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-7">
              
                  {dailog.active ? (
                  <div className="dailog-containter">
                    <div className={`alert alert-${dailog.status === 'error'? 'danger': 'success'}`}>
                      {dailog.message}
                    </div>
                  </div>
                ) : (
                  <></>
                )}

                  <div className="form-group row">
                    <label htmlFor="price" className="col-sm-4 col-form-label">
                      Price
                    </label>
                    <div className="col-sm-8">
                      <input
                        type="text"
                        className="form-control"
                        required
                        name="price"
                        placeholder="price"
                        value={formData.price}
                        onChange={handleChange}
                      />
                    </div>
                  </div>
                  <div className="form-group row">
                    <label htmlFor="currency" className="col-sm-4 col-form-label">
                      Currency
                    </label>
                    <div className="col-sm-8">
                      <select
                        type="text"
                        className="form-control"
                        required
                        name="currency"
                        placeholder="currency"
                        value={formData.currency}
                        onChange={handleChange}
                      >
                        <option value="ETH">ETH</option>
                        <option value="KOKO">KOKO</option>
                      </select>
                    </div>
                  </div>
                  <div className="form-group row">
                    <label htmlFor="currency" className="col-sm-4 col-form-label">
                      Auction Time
                    </label>
                    <div className="col-sm-8">
                      <select
                        type="text"
                        className="form-control"
                        required
                        name="time"
                        placeholder="time"
                        value={formData.time}
                        onChange={handleChange}
                      >
        
                        <option value="1">1 Day</option>
                        <option value="2">2 Days</option>
                        <option value="3">3 Days</option>
                        <option value="4">4 Days</option>
                        <option value="5">5 Days</option>
                        <option value="6">6 Days</option>
                        <option value="7">7 Days</option>
                        <option value="8">8 Days</option>
                      </select>
                    </div>
                  </div>

                  <div className="form-group row pt-3">
                    <div className="col-sm-12 text-center">
                      {buttonState.listing ? (
                        <button className="btn btn-info">Listing.. Please wait!</button>
                      ) : (
                        <button className="btn btn-primary" onClick={handleListNFT}> List your NFT </button>
                      )}
                    </div>
                  </div>
                
              </div>
            </div>
          </div>
        </div>
       {/*} <img
          src="/assets/images/Group-29.svg"
          className="img-fluid community-right-image"
          alt="bg"
                      />*/}
      </div>
    </section>
  )
}
export default List
