import React, { useState, useEffect } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { MDBInput } from 'mdb-react-ui-kit';
// import 'mdb-react-ui-kit/dist/css/mdb.min.css'

import ipfs from '../../services/ipfs'
import { patchUser } from '../../store/user/userSlice'

import Loading from '../../components/Loading'
import Clip from '../../components/Clip'

import { IMG_BASE } from '../../constant'
import { readImg, isVideo,isImage } from '../../utils'

import emptyAvatar from '../../images/avatar.svg'

const EditProfile = (props) => {
  const history = useHistory()
  const dispatch = useDispatch()

  const userSlice = useSelector((state) => state.user)
  const user = userSlice.user
  if (userSlice.status === 'failed') {
    console.log(userSlice.error)
  }

  const [coverImage, setCoverImage] = useState({
    url: user.coverImg ? IMG_BASE + user.coverImg : '',
    data: null,
    type: isVideo(user.coverImg) ? 'video' : 'image',
  })
  const [avatarImage, setAvatarImage] = useState({
    url: user.avatar ? IMG_BASE + user.avatar : emptyAvatar,
    data: null,
    type: isVideo(user.vatar) ? 'video' : 'image',
  })

  const [formData, setFormData] = useState({
    username: user.username ? user.username : '',
    email: user.email ? user.email : '',
    fullname: user.fullname ? user.fullname : '',
    mobile: user.mobile ? user.mobile : '',
    bio: user.bio ? user.bio : '',
    avatar: user.avatar ? user.avatar : emptyAvatar,
    coverImg: user.coverImg ? user.coverImg : '',
    instagram: user.instagram ? user.instagram : '',
    youtube: user.youtube ? user.youtube : '',
    tiktok: user.tiktok ? user.tiktok : '',
    facebook: user.facebook ? user.facebook : '',
    button: false,
  })

  useEffect(()=>{
    setFormData({
      username: user.username ? user.username : '',
      email: user.email ? user.email : '',
      fullname: user.fullname ? user.fullname : '',
      mobile: user.mobile ? user.mobile : '',
      bio: user.bio ? user.bio : '',
      avatar: user.avatar ? user.avatar : emptyAvatar,
      coverImg: user.coverImg ? user.coverImg : '',
      instagram: user.instagram ? user.instagram : '',
      youtube: user.youtube ? user.youtube : '',
      tiktok: user.tiktok ? user.tiktok : '',
      facebook: user.facebook ? user.facebook : '',
      button: false,
    })
    setAvatarImage({
      url: user.avatar ? IMG_BASE + user.avatar : emptyAvatar,
      data: '',
      type: isVideo(user.vatar) ? 'video' : 'image', 
    })
    setCoverImage({
      url: user.coverImg ? IMG_BASE + user.coverImg : '',
      data: '',
      type: isVideo(user.coverImg) ? 'video' : 'image',
    })

  }, [user])

  const handleChange = ({ target }) => {
    setFormData({ ...formData, [target.name]: target.value })
  }

  const handleOnSubmit = async (e) => {
    e.preventDefault()
    try{
      if (userSlice.status === 'pending' || formData.button) {
        return
      }
      setFormData({ ...formData, button: true })
  
      let avatarPath = '/nft.jpg'
      let coverImgPath = '/nft.jpg'
      let avatarHash = user.avatar
      let coverImgHash = user.coverImg
  
      if (avatarImage.type === 'video') {
        avatarPath = '/nft.mp4'
      }
      if (coverImage.type === 'video') {
        coverImgPath = '/nft.mp4'
      }
   //   only add if its there
      let ipfsAvatarResponse = null
      let ipfsCoverImgResponse = null
  
      if (avatarImage.data !== '') {
        ipfsAvatarResponse = await ipfs.add(
          {
            path: avatarPath,
            content: avatarImage.data,
          },
          { wrapWithDirectory: true },
        )
  
        avatarHash = ipfsAvatarResponse[1].hash
        avatarHash = avatarHash + avatarPath
      }
  
      if (coverImage.data !== '') {
        ipfsCoverImgResponse = await ipfs.add(
          {
            path: coverImgPath,
            content: coverImage.data,
          },
          { wrapWithDirectory: true },
        )
  
        coverImgHash = ipfsCoverImgResponse[1].hash
        coverImgHash = coverImgHash + coverImgPath
      }
  
      let formdata = {
        ...formData,
        avatar: avatarHash,
        coverImg: coverImgHash,
      }
      console.log(formdata)


      dispatch(patchUser(formdata))
  
      setFormData({ ...formData, button: false })
  
    }catch(err){
      console.log(err)
      setFormData({ ...formData, button: false })
    }
    // history.push('/' + formData.username)
  }

  const handleImgUpload = async (e) => {
    e.stopPropagation()
    e.preventDefault()
    const file = e.target.files[0]
    const name = e.target.files[0].name
    let fileType = 'image'
     let type = e.target.name
    if (isVideo(name)) {
      fileType = 'video'
    }else if(isImage(name)){
      fileType = 'image'
    }else{
      return
    }




    let imgUrl = await readImg(file, 'read')
    let imgData = await readImg(file, 'data')

    const imgBuffer = await Buffer.from(imgData)

   

     if (type === 'coverImg') {

      setCoverImage({
        ...coverImage,
        url: imgUrl,
        data: imgBuffer,
        type: fileType,
      })
   } else {

      setAvatarImage({
        ...avatarImage,
        url: imgUrl,
        data: imgBuffer,
        type: fileType,
      })
     }

    // setFormData({
    //   ...formData,
    //   imgUrl: imgUrl,
    //   imgData: imgBuffer,
    //   fileType: fileType,
    // })
  }

  if (!userSlice.user) {
    // TODO: maybe logo out
    // dispatch()
    history.push('/')
  }

  if (userSlice.status === 'idle' || userSlice.status === 'loading') {
    return <Loading />
  }

  return (
    <section className="join-the-community">
          <div className="container  pb-4">
            <img src="/assets/images/figures.png" className="img-fluid community-left-image" alt="bg"/>
            <div className="row">
               <div className="col-xl-10 mx-auto  edit-profile ">
                  {/*<img src="/assets/images/Componentback.svg" className="img-fluid community-backgrd-image" alt="bg"/>*/}
                  <h2 className="pl-5">Edit Profile</h2>
                  <p className="pl-5">You can set preferred display name, create your profile URL and manage other personal settings.</p>
               </div>
               <div className="col-xl-10 mx-auto edit-profile">

               <form onSubmit={handleOnSubmit}>
                      <div className="row d-flex">
                        <div className="col-lg-3 d-flex align-items-baseline justify-content-end">
                          {avatarImage.type === 'video' ? (
                            <Clip url={avatarImage.url} />
                          ) : (
                            <img className="img-fluid " src={avatarImage.url} alt="avatar"></img>
                          )}
                          {/* <img src="/assets/images/creator.png" className="img-fluid" /> */}

                          <div className="edit-profile">
                            <label htmlFor="file-upload" className="custom-file-upload">
                              <i className="fa fa-camera"></i>
                            </label>
                            <input
                              id="file-upload"
                              type="file"
                              name="avatar"
                              accept="image/*"
                              onChange={handleImgUpload}
                            />
                          </div>
                        </div>
                        <div className="col-lg-9 d-flex align-items-center justify-content-center flex-column">
                   
                        <div className="form-group">
                        <div className="form-outline  mb-4">
                          <p className="mb-4" style={{fontWeight:'500'}}>Account Info</p>
                          <MDBInput
                            label="Full Name"
                            type="text"
                            className="form-control"
                            name="fullname"
                            value={formData.fullname}
                            onChange={handleChange}
                            required
                          />
         
                        </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline  mb-4">

                          <MDBInput
                            label="Username"
                            type="text"
                            name="username"
                            value={formData.username}
                            onChange={handleChange}
                            required
                          />
            
                          </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline  mb-4">

                          <MDBInput
                            label="Email"
                            type="email"
                            className="form-control"
                            name="email"
                            value={formData.email}
                            onChange={handleChange}
                            required
                          />
          
                        </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline  mb-4">

                          <MDBInput
                            label="Phone Number"
                            type="text"
                            className="form-control"
                            name="mobile"
                            value={formData.mobile}
                            onChange={handleChange}
                          />
          
                        </div>
                        </div>
                        <div className="form-group mb-4">
                         
                          <MDBInput
                            label="Bio"
                            type="text"
                            className="form-control lay"
                            name="bio"
                            value={formData.bio}
                            onChange={handleChange}
                            textarea rows={4}
                          />
     

                        </div>
                    
                 

                        <div className="form-group">
                        <div className="form-outline  mb-4 mt-2">

                          <MDBInput
                            label="Your Telegram Link"
                            type="text"
                            className="form-control"
                            name="instagram"
                            value={formData.instagram}
                            onChange={handleChange}
                          />
                      
                        </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline  mb-4">

                          <MDBInput
                            label="Your Twitter Link"
                            type="text"
                            className="form-control"
                            name="youtube"
                            value={formData.youtube}
                            onChange={handleChange}
                          />
             
                        </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline  mb-4">

                          <MDBInput
                            label=" Your Facebook Link"
                            type="text"
                            className="form-control"
                            name="facebook"
                            value={formData.facebook}
                            onChange={handleChange}
                          />
               
                        </div>
                        </div>
                        <div className="form-group">
                        <div className="form-outline col-lg-5 m-auto d-flex justify-content-center align-items-center mb-4">

                          <MDBInput
                            label=" Your Discord Link"
                            type="text"
                            className="form-control"
                            name="tiktok"
                            value={formData.tiktok}
                            onChange={handleChange}
                          />
                          
                        </div>
                        <div className="form-group pt-3 align-items-center justify-content-center d-flex m-auto ">
                         <div className="row align-items-center d-flex justify-content-center">
                           <div className="">
                          <input type="checkbox" id="" name="" value=""/>
                        </div>
                         <div className="col-lg-6">
                           <label for=""> i've confirm that i have read and agree to the 
                           <a href="#">terms of the E-SIGN</a> Act Disclosure above.</label>
                           </div>
                           </div>
                        </div>
                        </div>
                        
                        </div>
                     


                      <div className="m-auto layer form-group pt-3">
                        <div className="form-group pt-3">
                          <div className="col-sm-12 text-center">
                            <button type="submit" className="btn btn-danger">
                              {formData.button? 'Updating..': 'Update'}
                            </button>
                          </div>
                        </div>
                      </div>
                        
                        
                        {user.role !== "creator"? (
                           <div className="col-lg-12 form-group ">
                           <div className="form-group ">
                             <div className="col-sm-12 text-center">
                    
                             </div>
                           </div>
                         </div>
                        ):(<></>)}
                        
                      </div>
                    </form>

                </div>

            </div>
           
         </div>

    </section>
  )
}

export default EditProfile
