import React, { useEffect, useState, useRef } from "react";
import { Overlay, Tooltip } from "react-bootstrap";
import { followCreator } from '../../api/user'
import AvatarImg from "../../images/avatar.png";
import { Link } from 'react-router-dom';
import CoverImg from "../../images/cover.jpg";
import Clip from "../../components/Clip";
import { isVideo } from "../../utils";
import { IMG_BASE, MAIN_URL } from "../../constant";
import { updateUser } from '../../store/user/userSlice'
import { useSelector, useDispatch } from 'react-redux';
import { updateCreators } from '../../store/creator/creatorSlice'
const Header = ({ creator }) => {
  const dispatch = useDispatch()

  const [reportPopUp, setReportPopUp] = useState(false);
  const [report, setReport] = useState(false);
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [shareTooltip, setShareTooltip] = useState(false);

const [loadingFollow, setLoadingFollow] = useState(false)
  const shareTarget = useRef(null);
  const FollowButton = ({ user, creator }) => {
    let isFollowing = false
    if (user) {
      let following = user.following
      following.forEach((_following) => {
        if (_following === creator.publicAddress) {
          isFollowing = true
        }
      })
      if(loadingFollow){
        return (
          <button className="btn btn-danger  p-2 pr-3 pl-3"> Loading... </button>
        )
      }
      return (
        <button onClick={handleFollow} className="btn btn-danger p-2 pr-3 pl-3">
          {isFollowing ? 'Unfollow' : 'Follow'}
        </button>
      )
    }
    return <></>
  }
  const handleFollow = async () => {
    try {
      setLoadingFollow(true)
      let data = {
        creator: creator.publicAddress,
      }
      let response = await followCreator(accessToken, data)
      dispatch(updateCreators(response.creator))
      dispatch(updateUser(response.user))
      setLoadingFollow(false)

    } catch (err) {
      console.log(err)
      setLoadingFollow(false)

    }
  }
  const handleShare = () => {
    let shareLink = `${MAIN_URL}${
      creator.username ? creator.username : creator.publicAddress
    }`;

    setShareTooltip(true);
    navigator.clipboard.writeText(shareLink);
    setTimeout(() => {
      setShareTooltip(false);
    }, 1000);
  };

  return (
    <div className="breacrumb-section">
      <div className="container">
        <div className="row d-flex justify-content-end" >
      
        <div className="col-xl-4 d-flex justify-content-between p-0 mb-2 align-items-right" style={{marginTop:'240px'}}>
      {user? (
            <div className="col-xl-4 col-4 d-flex align-items-right">
            {user && user.publicAddress === creator.publicAddress ? (
              <Link style={{ textDecoration: 'none' }} to="/profile/edit">
                <button className="btn btn-danger">Edit </button>
              </Link>
            ) : (
              <span></span>
              // <FollowButton user={user} creator={creator} />
              
            )}</div>
          ):(<></>)}
{/* 
          <div className="col-xl-4 col-4 d-flex align-items-center  ">
            <div className="pl-2 d-flex align-items-center justify-content-center">
              <p className="mb-0 textColor">{creator.following.length}</p>
              <p className="ml-1 mb-0 textColor">Following</p>
            </div>
          </div>
          <div className="col-xl-4 col-4 d-flex align-items-center">
            <p className="mb-0 textColor">{creator.followers.length}</p>
            <p className="mb-0 ml-1 textColor">Followers</p>
          </div> */}
      </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
