import React, {useState ,useRef} from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import moment from 'moment'
import { followCreator } from '../../api/user'
import { updateCreators } from '../../store/creator/creatorSlice'
import { updateUser } from '../../store/user/userSlice'
import {IMG_BASE,MAIN_URL } from '../../constant'
import AvatarImg from '../../images/avatar.png'
import { Overlay, Tooltip } from "react-bootstrap";
const Sidebar = ({ creator }) => {
  const dispatch = useDispatch()
  const avatar = creator.avatar ? IMG_BASE + creator.avatar : AvatarImg
  const accessToken = useSelector((state) => state.user.accessToken)
  const user = useSelector((state) => state.user.user)
  const [reportPopUp, setReportPopUp] = useState(false);
  const [report, setReport] = useState(false);

  const [shareTooltip, setShareTooltip] = useState(false);
  const shareTarget = useRef(null);

  const handleShare = () => {
    let shareLink = `${MAIN_URL}${
      creator.username ? creator.username : creator.publicAddress
    }`;

    setShareTooltip(true);
    navigator.clipboard.writeText(shareLink);
    setTimeout(() => {
      setShareTooltip(false);
    }, 1000);
  };
  const [loadingFollow, setLoadingFollow] = useState(false)

  const handleFollow = async () => {
    try {
      setLoadingFollow(true)
      let data = {
        creator: creator.publicAddress,
      }
      let response = await followCreator(accessToken, data)
      dispatch(updateCreators(response.creator))
      dispatch(updateUser(response.user))
      setLoadingFollow(false)

    } catch (err) {
      console.log(err)
      setLoadingFollow(false)

    }
  }

  const getSubKey = (key) => {
    return `${key.slice(0, 5)}...${key.slice(key.length - 5, key.length)}`
  }

  const getUsername = ()=> {
    if(creator){
      let username = creator.username
      if( username === ""){
        username = (creator.publicAddress).slice(0,8) + '...'
        username = username.toUpperCase()
      }
      return username
    }
    return ""
   
  }

  const FollowButton = ({ user, creator }) => {
    let isFollowing = false
    if (user) {
      let following = user.following
      following.forEach((_following) => {
        if (_following === creator.publicAddress) {
          isFollowing = true
        }
      })
      if(loadingFollow){
        return (
          <button className="btn btn-danger"> Loading... </button>
        )
      }
      return (
        <button onClick={handleFollow} className="btn btn-danger">
          {isFollowing ? 'Unfollow' : 'Follow'}
        </button>
      )
    }
    return <></>
  }

  return (
    <div className="produts-sidebar p-4" style={{marginTop:'-150px', zIndex:'2'}}>
      <div className="filter-widget pb-4 first-grid">
        <div className="row ">
          <div className="col-xl-12 d-flex justify-content-center flex-column">
          <img src={avatar} className="profile-image"></img>
    
        <h4 style={{}} className="black-color m-auto pb-2 font-weight-bold">{creator.fullname}</h4>
          </div>

          <div className="col-xl-6">
        
           {/*} <button
              style={{ textTransform: 'uppercase' }}
              type="button"
              className="btn btn-warning"
            >
              #{creator._id.slice(0, 5)}
  </button>*/}
          </div>
          <div className="col-xl-12">
            <h5 className="pl-2 pt-2 uppercase">
              {getSubKey(creator.publicAddress)}
            </h5>
            
          </div>
          {creator.bio !== '' ? (
        <>
          <div className="filter-widget m-auto pt-2">
            
            
            <p className="text-center ">{creator.bio}</p>
          </div>
          {' '}
        </>
      ) : (
        <></>
      )}
        </div>
      </div>

      <div className="filter-widget desc-section">
      
          {/*<h6 className="black-color pb-4 pt-3">@{getUsername()}</h6>*/}
        <div className="row">
          
          
          <div className="col-xl-8 mr-auto ml-auto" style={{marginTop:'-30px'}}>
              <div className="verify-twitter1">
               {/*} <div className="form-group">
                  <input type="text" className="form-control mt-0" readOnly placeholder={getUsername()}/>
                       <img src="/assets/images/at.png" className="img-fluid border-right" style={{top:'7%'}}/>
      </div>*/}
               </div>
          </div>
         
        </div>
      </div>
      <div className="col-lg-6">
            <div className="breadcrumb-text ">
            <button
                ref={shareTarget}
                onClick={handleShare}
                className="btn btn-danger"
            
              >
                Share
              </button>
              <Overlay
                target={shareTarget.current}
                show={shareTooltip}
                placement="top"
              >
                {(props) => (
                  <Tooltip id="overlay-share-tooltip" {...props}>
                    Copied
                  </Tooltip>
                )}
              </Overlay>
              {reportPopUp ? (
                <div className="report-pop-up">
                  <p onClick={() => setReport(!report)}>Report</p>
                </div>
              ) : (
                <></>
              )}
              {report ? (
                <div className="report-pop-up">
                  <p onClick={() => setReport(!report)} className="">
                    Reported
                  </p>
                </div>
              ) : (
                <></>
              )}

              <img
                onClick={() => setReportPopUp(!reportPopUp)}
                src="assets/images/threedots.png"
                className="img-fluid"
              />

              
            </div>
          </div>
      {/*<div className="col-xl-12 d-flex justify-content-between p-0">
      {user? (
            <div className="col-xl-4 col-4">
            {user && user.publicAddress === creator.publicAddress ? (
              <Link style={{ textDecoration: 'none' }} to="/profile/edit">
                <button className="btn btn-primary">Edit</button>
              </Link>
            ) : (
              <FollowButton user={user} creator={creator} />
            )}</div>
          ):(<></>)}

          <div className="col-xl-4 col-4">
            <div className="pl-2">
              <strong>{creator.following.length}</strong>
              <p>Following</p>
            </div>
          </div>
          <div className="col-xl-4 col-4">
            <strong>{creator.followers.length}</strong>
            <p>Followers</p>
          </div>
            </div>*/}
    

      <div className="filter-widget">
     

        <ul className="links d-flex justify-content-center">
          {creator.instagram ? (
            <li>
              <a href={creator.instagram} target="_blank" rel="noreferrer">
                <img
                  src="/assets/images/social-media-icons/tel.svg"
                  className="mr-2"
                  style={{height:'30px' , borderRadius:'40px'}}
                />
               {/*{creator.instagram}*/}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.youtube ? (
            <li>
              <a href={creator.youtube} target="_blank" rel="noreferrer">
                <img
                  src="/assets/images/social-media-icons/twitter.svg"
                  className="mr-2"
                  style={{height:'30px' , borderRadius:'40px'}}
                />
                {/*{creator.youtube}*/}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.facebook ? (
            <li>
              <a href={creator.facebook} target="_blank" rel="noreferrer">
                <img
                  src="/assets/images/social-media-icons/fb.svg"
                  className="mr-1"
                  style={{height:'30px',  borderRadius:'40px'}}
                />
                {/*{creator.facebook}*/}
              </a>
            </li>
          ) : (
            <></>
          )}

          {creator.tiktok ? (
            <li>
              <a href={creator.tiktok} target="_blank" rel="noreferrer">
                <img
                  src="/assets/images/social-media-icons/discord.svg"
                  className="mr-2"
                  style={{height:'30px' , borderRadius:'40px'}}
                />
                {/*{creator.tiktok}*/}
              </a>
            </li>
          ) : (
            <></>
          )}
        </ul>
        <hr />
        
      </div>
   
      <div className="filter-widget">
        <div className="row">
         
          <div className="col-xl-12 col-12">
            <h6>Member since : {moment(creator.date).format('LL')}</h6>
          </div>
        </div>
      </div>
      <hr />
    </div>
  )
}

export default Sidebar
