const axios = require('axios').default
//axios.defaults.baseURL = 'https://hidden-woodland-62406.herokuapp.com/';
axios.defaults.headers.post['Content-Type'] = 'application/json'


export const getCategory = () => {
    return new Promise(async (resolve, reject) => {
      try {
        let response = await axios(`/api/setting/get/category`)
        resolve(response.data)
        return
      } catch (err) {
        console.log(err)
        return reject(err.message ? err.message : 'internal error')
      }
    })
  }

  
export const getETHRate = async (coin="ETH") => {
    if(coin && coin !=="ETH") {
      coin="KOKO"
    }
    return new Promise(async (resolve, reject) => {
      try {
        let response = await axios('https://min-api.cryptocompare.com/data/price?fsym=KOKO&tsyms=USD')
        const newData= {
          "exchange": "binance",
          "pair": "ethusdt",
          "price": response && response.data?response.data.USD:'0',
          "ask": response && response.data?response.data.USD:'0',
          "bid": response && response.data?response.data.USD:'0'
        }
        resolve(newData)
        return
      } catch (err) {
        console.log(err)
        return reject(err.message ? err.message : 'internal error')
      }
    })
  }
  